.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}
.my__logo {
	max-width: 150px;
}
h1 {
	font-family: 'Montserrat', sans-serif;
	font-weight: 800;
}
h2,
h3,
h4,
h5,
h6 {
	font-family: 'Montserrat', sans-serif;
	font-weight: 400;
}
p {
	font-family: 'Roboto', sans-serif;
}
a {
	text-decoration: none;
}
a:link,
a:visited,
a:hover,
a:active {
	color: #0064b7;
}

body::-webkit-scrollbar {
	width: 5px;
}

body::-webkit-scrollbar-track {
	box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
}

body::-webkit-scrollbar-thumb {
	background-color: #0091ea;
	outline: 1px solid transparent;
}

.my-portrait {
	width: 300px;
	height: 400px;
	border-radius: 50%;
	margin: 30px;
}
.about__me {
	background-color: #0064b7;
	padding: 5rem;
}
.about__me__text__ctn {
	margin: auto;
}
.about__me__text__ctn h3 {
	margin: 10px;
	font-size: 32px;
	font-weight: 600;
	letter-spacing: 1pt;
	color: rgba(255, 255, 255, 91%);
}
.about__me__text__ctn p {
	font-size: 18px;
	color: rgba(255, 255, 255, 91%);
}
.skills {
	margin-top: -5rem;
	background-color: rgb(255, 255, 255);
	width: 80%;
	margin-bottom: 4em;
	margin-left: auto;
	margin-right: auto;
	border-radius: 2rem;
	box-shadow: 0 2.5px 5px rgba(0, 0, 0, 0.16),
		0 2.5px 5px rgba(0, 0, 0, 0.23);
}
.skills__icn {
	color: #64c1ff;
	font-size: 4em;
	margin: 20px;
}
.last {
	padding-bottom: 1em;
	margin-bottom: 1em;
}
.col h4 {
	font-size: 18px;
	font-weight: 700;
}
.col h5 {
	font-size: 16px;
	font-weight: 600;
	color: #0064b7;
}
.col p {
	width: 80%;
	margin: auto;
}
.tools {
	list-style-type: none;
	padding: 0;
	margin: auto;
}
.tools li {
	padding: 5px;
}

@media screen and (min-width: 1024px) {
	.skills {
		display: flex;
		flex-direction: row;
		justify-content: space-evenly;
	}
	.col {
		flex: 1 1;
		padding-top: 1em;
		margin-top: 1em;
		padding-bottom: 1em;
		margin-bottom: 1em;
	}
	.first {
		border-right: 1px solid rgba(100, 193, 255, 0.4);
	}
	.last {
		border-left: 1px solid rgba(100, 193, 255, 0.4);
	}
	.about__me__text__ctn {
		width: 70%;
	}
}

.projects__header {
	padding: 20px;
	margin: 20px;
}
.projects__header h2 {
	margin: 10px;
	font-size: 32px;
	font-weight: 600;
	letter-spacing: 1pt;
}
.projects__header p {
	font-size: 20px;
}
.projects {
	padding: 30px;
	margin: auto;
}
.projects__ctn {
	max-width: 100%;
	display: flex;
	flex-direction: column;
}
.project {
	max-width: 100%;
	padding: 20px;
}
.project__img {
	max-width: 100%;
	height: auto;
}
.project__info {
	text-align: left;
}
.project__info h3 {
	font-weight: 600;
	color: #0064b7;
}
.project__info h4 {
	font-weight: 600;
}
.project__links {
	list-style-type: none;
	padding: 0;
}
.project__link__credentials {
	margin-top: 5px;
}
.project__link__credentials p {
	margin: 0;
}
hr {
	border: 0;
	height: 0;
	border-top: 1px solid rgba(100, 193, 255, 0.4);
	border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
@media screen and (min-width: 1024px) {
	.projects__ctn {
		max-width: 100%;
		display: flex;
		flex-direction: row;
	}
}

.contact {
	position: relative;
	background-color: #0091ea;
	max-width: 100%;
	padding: 20px;
	margin-top: 3em;
}
.contact__me {
	position: relative;
	display: flex;
	flex-direction: column;
	grid-gap: 10px;
	gap: 10px;
	justify-content: space-between;
	align-items: center;
	top: -4rem;
	margin: 10px auto;
	max-width: -webkit-fit-content;
	max-width: -moz-fit-content;
	max-width: fit-content;
	border-radius: 10px;
	padding: 10px;
	background-color: #002f6c;
	box-shadow: 0 2.5px 5px rgba(0, 0, 0, 0.16),
		0 2.5px 5px rgba(0, 0, 0, 0.23);
}
.contact__me a {
	flex: 1 1;
	border: 2px solid #0091ea;
	padding: 10px;
	border-radius: 2em;
	max-width: -webkit-fit-content;
	max-width: -moz-fit-content;
	max-width: fit-content;
	color: #fff;
	transition: 0.35s ease-in-out;
}
.contact__me a:hover {
	background-color: #0091ea;
	color: white;
}
.contact__me h2 {
	color: #fff;
	font-weight: 500;
	padding: 10px;
	margin: 0;
}
.contact h3 {
	color: #fff;
	font-size: 24px;
	font-weight: 600;
	letter-spacing: 1pt;
}
.contact__me p {
	flex: 1 1;
	color: #fff;
	padding: 10px;
	margin: 0;
}

.contact__icn {
	font-size: 3em;
	margin: 5px;
	color: #002f6c;
	cursor: pointer;
}
.contact__icn:hover {
	color: #fff;
}
.copyright {
	font-weight: 500;

	font-size: 14px;
}
@media screen and (min-width: 1024px) {
	.contact__me {
		flex-direction: row;
	}
}

.main__header {
	height: 100px;
}
.is-transparent {
	background-color: transparent;
}
.navigation {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: auto;
	width: 90%;
}
.navigation a {
	text-decoration: none;
	font-size: 18px;
	font-weight: 500;
	border: 2px solid transparent;
	padding: 10px;
	margin: 5px;
	border-radius: 2em;
	transition: 0.35s ease-in-out;
}
.navigation a:hover {
	border: 2px solid;
}

.contact__me__form {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: auto;
}
.contact__me__form h1 {
	max-width: 70%;
	font-size: 24px;
	font-weight: 600;
}
.contact__me__form label {
	font-family: 'Roboto', sans-serif;
}
.contact__me__form input {
	font-family: 'Roboto', sans-serif;
	margin: 5px auto;
	padding: 10px;
	border: 1px solid #ccc;
	border-radius: 4px;
}
.contact__me__form textarea {
	padding: 10px;
	margin: 5px auto;
	border: 1px solid #ccc;
	border-radius: 4px;
	min-width: 200px;
	min-height: auto;
}
.contact__me__form p {
	font-size: 24px;
	font-weight: 500;
	font-family: 'Roboto', sans-serif;
}
.contact__me__form button {
	margin-top: 10px;
	background-color: transparent;
	border: 2px solid transparent;
	padding: 10px;
	color: #0064b7;
	font-size: 20px;
	font-weight: 500;
	border-radius: 2em;
	font-family: 'Roboto', sans-serif;
	cursor: pointer;
	transition: 0.25s ease-in;
}
.contact__me__form button:hover {
	border: 2px solid #0064b7;
}

